/* eslint-disable no-nested-ternary */

import { Placement, PositioningStrategy } from '@popperjs/core';
import composeRefs from '@seznam/compose-react-refs';
import { AnimatePresence, HTMLMotionProps, motion } from 'framer-motion';
import React, { PropsWithChildren } from 'react';
import { createFramerTransition } from '../../../hooks/transitions';
import { useID } from '../../../hooks/use-id';
import { useIsomorphicLayoutEffect } from '../../../hooks/use-isomorphic-layout-effect';
import { clsx } from '../../../libs/clsx';
import { createErrorWithCode } from '../../../libs/exceptions';
import { forwardRefWrapper } from '../../../libs/forward-ref-wrapper';
import { mergeProps } from '../../../libs/merge-props';
import { IntrinsicElementProps } from '../../../types/utility';
import { ThemeProvider, useTheme } from '../../theme-provider';
import { AutoResizingDiv } from '../auto-resizing-div';
import { EphemeralPortal } from '../ephemeral-portal';
import { PopperWrapper, usePopperWrapper } from './use-popper-wrapper';

import styles from './popover.less';

interface PopoverBaseProps extends IntrinsicElementProps<'div'> {
  in?: boolean;
  arrow?: boolean;
  offset?: [number, number];
  placement?: Placement;
  strategy?: PositioningStrategy;
  portalize?: boolean;
  appearance?: 'default' | 'none';
  stretch?: boolean;
}

interface PassthroughProps {
  _in?: boolean;
  _portalize?: boolean;
  _hasArrow?: boolean;
  _popper: PopperWrapper;
  _appearance?: 'default' | 'none';
  _stretch?: boolean;
}

const PopoverBase = /* @__PURE__ */ forwardRefWrapper<HTMLDivElement, PopoverBaseProps>(
  'Popover',

  {
    in: false,
    arrow: true,
    placement: 'bottom',
    strategy: 'absolute',
    portalize: false,
    appearance: 'default',
    stretch: false,
  },

  (props, externalRef) => {
    const {
      children,
      in: inProp,
      portalize,
      placement,
      arrow,
      strategy,
      offset,
      appearance,
      stretch,
      className,
      ...otherProps
    } = props;

    const popper = usePopperWrapper({ arrow, placement: placement!, strategy: strategy!, offset });
    const passthroughProps: PassthroughProps = {
      _portalize: portalize,
      _in: inProp,
      _hasArrow: arrow,
      _popper: popper,
      _appearance: appearance,
      _stretch: stretch,
    };

    const anchor = React.Children.map(children, (child) => {
      if (React.isValidElement(child) && child.type === PopoverAnchor) {
        return React.cloneElement(child, mergeProps(child.props, { ...passthroughProps }));
      }

      return null;
    })?.filter(Boolean);

    const content = React.Children.map(children, (child) => {
      if (React.isValidElement(child) && child.type === PopoverContent) {
        return React.cloneElement(child, mergeProps(child.props, { ...passthroughProps }));
      }

      return null;
    })?.filter(Boolean);

    // Validate that only one <Popover.Anchor> is received.
    const numAnchors = React.Children.count(anchor);
    if (numAnchors > 1) {
      throw createErrorWithCode(
        'POPOVER_ONLY_ONE_ANCHOR_ALLOWED',
        PopoverBase,
        `Only one <Popover.Anchor> element can be rendered in a <Popover> at a time. Received: ${numAnchors}`,
      );
    }

    // Validate that only one <Popover.Content> is received.
    const numContents = React.Children.count(content);
    if (numContents > 1) {
      throw createErrorWithCode(
        'POPOVER_ONLY_ONE_CONTENT_ALLOWED',
        PopoverBase,
        `Only one <Popover.Content> element can be rendered in a <Popover> at a time. Received: ${numContents}`,
      );
    }

    if (React.Children.count(children) > numAnchors + numContents) {
      // Validate that only <Popover.Content> and <Popover.Anchor> components are received as children.
      throw createErrorWithCode(
        'POPOVER_INCOMPATIBLE_CHILDREN_DETECTED',
        PopoverBase,
        'Only children wrapped with <Popover.Anchor> or <Popover.Content> are allowed within a <Popover>.',
      );
    }

    return (
      <div className={clsx([styles.Popover, className])} {...otherProps} ref={externalRef}>
        {anchor}
        {content}
      </div>
    );
  },
);

const PopoverAnchor = /* @__PURE__ */ forwardRefWrapper<HTMLDivElement, IntrinsicElementProps<'div'>>(
  'Popover.Anchor',

  (props, externalRef) => {
    const { children, _in, _portalize, _hasArrow, _popper, _appearance, _stretch, ...otherProps } =
      props as typeof props & PassthroughProps;

    return (
      <div {...otherProps} ref={composeRefs<HTMLDivElement>(_popper.anchorProps.ref, externalRef)}>
        {children}
      </div>
    );
  },
);

function getPopoverContentTransitionRotation(placement?: Placement) {
  switch (placement) {
    case 'top':
      return { rotateX: '-7deg', rotateY: '0deg', transformOrigin: 'bottom center' };
    case 'top-start':
      return { rotateX: '7deg', rotateY: '7deg', transformOrigin: 'bottom left' };
    case 'top-end':
      return { rotateX: '7deg', rotateY: '-7deg', transformOrigin: 'bottom right' };

    case 'right':
      return { rotateX: '0deg', rotateY: '7deg', transformOrigin: 'left center' };
    case 'right-start':
      return { rotateX: '-7deg', rotateY: '7deg', transformOrigin: 'left top' };
    case 'right-end':
      return { rotateX: '7deg', rotateY: '7deg', transformOrigin: 'left bottom' };

    case 'left':
      return { rotateX: '0deg', rotateY: '-7deg', transformOrigin: 'right center' };
    case 'left-start':
      return { rotateX: '-7deg', rotateY: '-7deg', transformOrigin: 'right top' };
    case 'left-end':
      return { rotateX: '7deg', rotateY: '-7deg', transformOrigin: 'right bottom' };

    case 'bottom':
      return { rotateX: '-7deg', rotateY: '0deg', transformOrigin: 'top center' };
    case 'bottom-start':
      return { rotateX: '-7deg', rotateY: '7deg', transformOrigin: 'top left' };
    case 'bottom-end':
      return { rotateX: '-7deg', rotateY: '-7deg', transformOrigin: 'top right' };

    default:
      return { rotateX: '0deg', rotateY: '0deg', transformOrigin: 'center' };
  }
}

const popoverContentTransition = /* @__PURE__ */ createFramerTransition<{ placement?: Placement }>().withVariants({
  initial: ({ placement }) => ({
    opacity: 0,
    scale: 0.96,
    ...getPopoverContentTransitionRotation(placement),
    transition: { type: 'tween', ease: 'easeInOut', duration: 0.2 },
    perspective: '500px',
  }),

  animate: () => ({
    opacity: 1,
    scale: 1,
    rotateX: '0deg',
    rotateY: '0deg',
    transition: { type: 'tween', ease: 'easeInOut', duration: 0.2 },
    perspective: '500px',
  }),
});

const PopoverContent = /* @__PURE__ */ forwardRefWrapper<
  HTMLDivElement,
  HTMLMotionProps<'div'> & PropsWithChildren<IntrinsicElementProps<'div'>>
>(
  'Popover.Content',

  (props, externalRef) => {
    const { children, className, _in, _portalize, _hasArrow, _popper, _appearance, _stretch, ...otherProps } =
      props as typeof props & PropsWithChildren<PassthroughProps>;

    const id = useID();
    const theme = useTheme();

    const getPopoverContentTransitionProps = popoverContentTransition.use({
      initial: 'initial',
      animate: 'animate',
      exit: 'initial',
    });

    useIsomorphicLayoutEffect(() => {
      _popper.forceUpdate?.();
    }, [_in]);

    const content = (
      <div
        className={clsx([
          styles.PopoverContainer,
          _stretch && styles[_popper.state?.placement.match(/(top|bottom)/) ? 'stretchHorizontal' : 'stretchVertical'],
          _hasArrow && _appearance === 'default' && styles.hasArrow,
        ])}
        {..._popper.contentProps}
      >
        <AnimatePresence>
          {_in && (
            <motion.div
              key={id}
              className={clsx([styles.PopoverContent, _appearance === 'none' && styles.appearanceNone, className])}
              {...otherProps}
              {...getPopoverContentTransitionProps({ placement: _popper.state?.placement })}
              ref={externalRef}
            >
              <AutoResizingDiv observerOnly onResize={_popper.update as any}>
                {children}
              </AutoResizingDiv>
            </motion.div>
          )}
        </AnimatePresence>
      </div>
    );

    return _portalize ? (
      <EphemeralPortal className={styles.PopoverPortal}>
        <ThemeProvider theme={theme}>
          <div className={styles.Popover}>{content}</div>
        </ThemeProvider>
      </EphemeralPortal>
    ) : (
      content
    );
  },
);

export const Popover = /* @__PURE__ */ Object.assign(PopoverBase, { Anchor: PopoverAnchor, Content: PopoverContent });
